@import url(https://fonts.googleapis.com/css?family=Russo+One);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  color: #fff;
  font-weight: lighter;
  text-align: center;
  padding: 40px; }

.lightOn {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: #2efbdf;
  border: 1px solid #23b6a4;
  box-shadow: 0px 0px 10px 5px #2efbdf; }

.lightOff {
  width: 15px;
  height: 15px;
  border-radius: 50%; }

body {
  margin: 0;
  padding: 0;
  font-family: Russo One;
  background: #222; }
  body #root {
    width: 100%;
    background: #222;
    display: flex;
    justify-content: center;
    align-items: center; }
    body #root #drum-machine {
      background: #2a2a2a;
      display: flex;
      border: 1px solid #151515;
      border-radius: 3px;
      box-shadow: 0 0 20px 0px #000;
      max-width: 450px;
      width: 80%;
      padding: 20px; }
    body #root #drums {
      max-width: 330px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      margin-right: 5px; }
      body #root #drums .drum-padOff {
        width: 100px;
        height: 100px;
        border: 1px solid #020202;
        border-radius: 2px;
        background: #cfcfcf;
        cursor: pointer;
        text-align: center;
        box-shadow: inset 0 0 2px 1px black;
        margin: 5px auto; }
        body #root #drums .drum-padOff .light {
          width: 10px;
          height: 10px;
          border-radius: 50%;
          position: relative;
          left: 10px;
          top: 20px;
          background: #848788;
          border: 1px solid #adadad;
          box-shadow: inset 0 0 5px #474646; }
        body #root #drums .drum-padOff p {
          position: relative;
          top: 15px; }
      body #root #drums .drum-padOn {
        width: 100px;
        height: 100px;
        border: 1px solid #020202;
        border-radius: 2px;
        background: #cfcfcf;
        cursor: pointer;
        text-align: center;
        box-shadow: inset 0 0 2px 1px black;
        margin: 5px auto; }
        body #root #drums .drum-padOn .light {
          width: 10px;
          height: 10px;
          border-radius: 50%;
          position: relative;
          left: 10px;
          top: 20px;
          background: #848788;
          border: 1px solid #adadad;
          box-shadow: inset 0 0 5px #474646; }
        body #root #drums .drum-padOn p {
          position: relative;
          top: 15px; }
      body #root #drums .drum-padOn:active {
        position: relative;
        z-index: 1;
        background: #fed0d0;
        border: 1px solid #020202bf;
        box-shadow: 0 0 10px 0px #fed0d0; }
      body #root #drums .drum-padOn:active .light {
        background: #2efbdf;
        border: 1px solid #23b6a4;
        box-shadow: 0px 0px 10px 5px #2efbdf; }
    body #root #functionality {
      height: 254px;
      width: 114px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around; }
      body #root #functionality #displayOff {
        width: 100%;
        height: 40px;
        margin-top: 7px;
        text-align: center;
        line-height: 38px;
        background: #848788;
        box-shadow: inset 0 0 5px #474646; }
      body #root #functionality #displayOn {
        width: 100%;
        height: 40px;
        margin-top: 7px;
        text-align: center;
        line-height: 38px;
        background: #fff;
        border: 1px solid #000000a6;
        box-shadow: 0 0 6px 0px #fff; }
      body #root #functionality #bank {
        color: #fff;
        display: flex;
        flex-direction: column;
        align-items: center; }
        body #root #functionality #bank #bank-switch {
          width: 60px;
          height: 20px;
          border: 3px solid #0000008c;
          background: #000;
          cursor: pointer; }
          body #root #functionality #bank #bank-switch #bank1 {
            height: 100%;
            width: 50%;
            background: #673AB7;
            float: left; }
          body #root #functionality #bank #bank-switch #bank2 {
            height: 100%;
            width: 50%;
            background: #673AB7;
            float: right; }
      body #root #functionality #power {
        display: flex;
        background: #474747;
        border: 2px solid #202020;
        border-radius: 10px;
        justify-content: space-between;
        width: 70px;
        padding: 10px 10px;
        cursor: pointer;
        color: #fff; }
        body #root #functionality #power .lightOn {
          width: 15px;
          height: 15px;
          border-radius: 50%;
          background: #2efbdf;
          border: 1px solid #23b6a4;
          box-shadow: 0px 0px 10px 5px #2efbdf; }
        body #root #functionality #power .lightOff {
          width: 15px;
          height: 15px;
          border-radius: 50%;
          background: #848788;
          border: 1px solid #adadad;
          box-shadow: inset 0 0 5px #474646; }

@media (max-width: 540px) {
  body #root {
    padding: 20px auto; }
  body #root #drum-machine {
    align-items: center;
    flex-direction: column; }
  body #root #functionality {
    height: 196px; } }

@media (max-width: 410px) {
  body #root #drums .drum-padOff {
    width: 80px;
    height: 80px; }
  body #root #drums .drum-padOff p {
    position: relative;
    top: 0; }
  body #root #drums .drum-padOn {
    width: 80px;
    height: 80px; }
  body #root #drums .drum-padOn p {
    position: relative;
    top: 0; } }

@media (max-width: 330px) {
  body #root #drums .drum-padOff {
    width: 70px;
    height: 70px; }
  body #root #drums .drum-padOn {
    width: 70px;
    height: 70px; } }

